.app-style {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #1f1f1f;
  font-size: x-large;
  color: #f5f5f5;
  background-image: url("https://www.warcrafttavern.com/wp-content/uploads/2022/11/Vault-of-the-Incarnates-Raid-Release-Schedule-Location-More.jpg");
  background-size: cover;
  background-position-y: center;
  background-position-x: center;
}

a:link,
a:visited {
  color: #ffd700;
  text-decoration: none;
  cursor: pointer;
}

a:link:active,
a:visited:active {
  color: #ffd700;
}
